import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardHeader from "@material-ui/core/CardHeader";
import CardMedia from "@material-ui/core/CardMedia";
import CardContent from "@material-ui/core/CardContent";
import Typography from "@material-ui/core/Typography";
import { Grid } from "@material-ui/core";
import Udangkrispi from "../Card/udang.jpg";
import Jamurkrispi from "../Card/jamur.jpg";
import Kentangkrispi from "../Card/kentang.jpg";
const useStyles = makeStyles((theme) => ({
  root: {
    maxWidth: 300,
    height: 380,
    backgroundColor: "#dbf6e9",
    [theme.breakpoints.down("sm")]: {
      maxWidth: 250,
      height: "fit-content",
    },
  },
  media: {
    height: 0,
    paddingTop: "56.25%", // 16:9
    backgroundSize: "cover",
    backgroundPosition: "center",
  },
  container: {
    marginTop: 30,
    marginBottom: 30,
    [theme.breakpoints.down("sm")]: {
      gap: 20,
    },
  },
  gridItem: {
    flexBasis: "auto",
  },
}));

export default function CardPath() {
  const classes = useStyles();

  return (
    <Grid
      className={classes.container}
      container
      alignItems="center"
      justify="space-evenly"
      wrap="wrap"
    >
      <Grid xs={12} lg={3} md={4} className={classes.gridItem}>
        <Card className={classes.root} elevation={5}>
          <CardHeader
            title="Udang Krispi"
            subheader="Chef Juna"
            titleTypographyProps={{ variant: "h6" }}
          />
          <CardMedia className={classes.media} image={Udangkrispi} title="Udang Krispi" />
          <CardContent>
            <Typography variant="body2" color="textSecondary" component="p">
              Sangat enak di makan saat nyatai dan di cocol dengan saus sambal pedas.Menu ini dapat
              di hidangkan dengan nasi agar cepat kenyang
            </Typography>
          </CardContent>
        </Card>
      </Grid>
      <Grid xs={12} lg={3} md={4} item className={classes.gridItem}>
        <Card className={classes.root} elevation={5}>
          <CardHeader title="Jamur Krispi" subheader="Chef Arnold" />
          <CardMedia className={classes.media} image={Jamurkrispi} title="Jamur Krispi" />
          <CardContent>
            <Typography variant="body2" color="textSecondary" component="p">
              Lorem ipsum dolor sit amet, consectetur adipisicing elit. Nam pariatur dolores laborum
              ab? Commodi, quis blanditiis.
            </Typography>
          </CardContent>
        </Card>
      </Grid>
      <Grid xs={12} lg={3} md={12} item className={classes.gridItem}>
        <Card className={classes.root} elevation={5}>
          <CardHeader title="Kentang Krispi" subheader="Chef Juna" />
          <CardMedia className={classes.media} image={Kentangkrispi} title="Kentang Krispi" />
          <CardContent>
            <Typography variant="body2" color="textSecondary" component="p">
              Lorem ipsum dolor sit amet consectetur adipisicing elit. Suscipit nihil similique,
              officiis voluptas iste pariatur sit dolorum omnis molestias maiores.
            </Typography>
          </CardContent>
        </Card>
      </Grid>
    </Grid>
  );
}
