import React from "react";
import ReactDOM from "react-dom";
import AppbarMaterial from "./Component/AppBar/Appbar";
import Jumbotron from "./Component/Jumbotron/Jumbotron";
import { createMuiTheme } from "@material-ui/core/styles";
import { ThemeProvider } from "@material-ui/core/styles";
import CardPath from "./Component/Card/Card";
const theme = createMuiTheme({
  palette: {
    primary: {
      main: "#a7c5eb",
      light: "#413c69",
    },
    secondary: {
      main: "#4a47a3",
    },
  },
  typography: {
    fontFamily: "Poppins",
    h6: {
      fontWeight: "bolder",
    },
    h4: {
      fontWeight: "bolder",
    },
    h5: {
      fontWeight: "bolder",
    },
    caption: {
      fontWeight: 400,
      fontSize: 18,
    },
  },
});
ReactDOM.render(
  <React.StrictMode>
    <ThemeProvider theme={theme}>
      <AppbarMaterial />
      <Jumbotron />
      <CardPath />
    </ThemeProvider>
  </React.StrictMode>,
  document.getElementById("root")
);
