import React from "react";
import { makeStyles } from "@material-ui/styles";
import { AppBar, Typography, Toolbar, Button } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  navbars: {
    display: "flex",
    justifyContent: "space-between",
  },
  buttonGroup: {
    width: "20%",
    display: "flex",
    justifyContent: "space-around",
    "&:hover": {
      color: "red",
    },
    [theme.breakpoints.down("sm")]: {
      width: "50%",
    },
  },
  homes: {
    "&:hover": {
      color: "red",
    },
  },
  buttonApp: {
    transition: "0.4s",
    fontWeight: 600,
    letterSpacing: 1,
    "&:hover": {
      backgroundColor: "transparent",
      color: "red",
    },
    "&:active": {
      color: "white",
    },
    [theme.breakpoints.down("sm")]: {
      fontSize: 15,
    },
  },
  appbar: {
    width: "100vw",
  },
}));

export default function AppbarMaterial() {
  const classes = useStyles();
  return (
    <div className={classes.root}>
      <AppBar position="static" color="secondary" className={classes.appbar}>
        <Toolbar variant="dense" className={classes.navbars}>
          <Typography variant="h6" color="inherit" color="primary" className={classes.title}>
            Material UI
          </Typography>
          <div className={classes.buttonGroup}>
            <Button color="primary" className={classes.buttonApp} disableTouchRipple>
              Home
            </Button>
            <Button color="primary" className={classes.buttonApp} disableTouchRipple>
              About
            </Button>
          </div>
        </Toolbar>
      </AppBar>
    </div>
  );
}
