import { Button, Grid, Paper, Typography } from "@material-ui/core";
import React from "react";
import { makeStyles } from "@material-ui/styles";
const useStyles = makeStyles((theme) => ({
  container: {
    flexGrow: 1,
  },
  paper: {
    height: 600,
    display: "flex",
    justifyContent: "center",
    flexDirection: "column",
    backgroundColor: "transparent",
    alignItems: "center",
    gap: 10,
    textAlign: "center",
    [theme.breakpoints.down("sm")]: {
      height: 400,
    },
  },
  foodOne: {
    width: "100%",
  },
}));
export default function Jumbotron() {
  const classes = useStyles();
  return (
    <div>
      <Grid container spacing={0} className={classes.container}>
        <Grid item xs={12} sm={6} justify="space-around" display="flex">
          <Paper xs={12} sm={6} elevation={0} square className={classes.paper}>
            <Typography variant="h4" color="primary">
              Toko Makanan Fajri
            </Typography>
            <Typography variant="caption">
              Porsi Mengeyangkan dan Harga Mulai dari Rp.15,000.
            </Typography>
            <Button
              color="secondary"
              variant="contained"
              style={{ width: "fit-content" }}
              size="large"
            >
              Start
            </Button>
          </Paper>
        </Grid>
        <Grid item xs={12} sm={6} justify="center" display="flex">
          <Paper xs={12} sm={6} elevation={0} square className={classes.paper}>
            <img src="/image/food1.png" alt="" className={classes.foodOne} />
          </Paper>
        </Grid>
      </Grid>
    </div>
  );
}
